.header {
  padding: 30px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__logo {
    width: 200px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__btn {
    height: 42px;
    gap: 10px;
    padding: 3px 45px;
    .btn-text {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 24px 0;
    &__logo {
      width: 120px;
    }
    &__btn {
      padding: 2px 24px;
      .btn-text {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    padding: 20px 0;
    &__logo {
      width: 80px;
    }
    &__btn {
      height: 32px;
      gap: 5px;
      padding: 2px 16px;
      .btn-text {
        font-size: 12px;
      }
    }
  }
}