.main {
  padding-bottom: 60px;
  &__inner {
    background-image: url("../img/main-img.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 20px;
    padding: 30px 60px;
  }
  &__group {
    max-width: 800px;
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(6px);
    border-radius: 20px;
    padding: 40px;
  }
  &__title {
    font-size: 48px;
    line-height: 130%;
    font-weight: 500;
  }
  &__subtitle {
    font-size: 30px;
    line-height: 130%;
    font-weight: 500;
    margin-bottom: 40px;
  }
  &__list {
    margin-bottom: 40px;
    li {
      font-size: 21px;
      line-height: 130%;
    }
  }
  &__btn {
    height: 58px;
    gap: 10px;
    padding: 5px 50px;
    .btn-text {
      font-size: 18px;
    }
  }
}
.info {
  padding: 60px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 15px;
  }
  &__desc {
    width: 330px;
    flex-shrink: 0;
    background: #212431;
    border-radius: 20px;
    padding: 30px;
    color: #fff;
  }
  &__images {
    width: calc(100% - 345px);
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 30px / 3));
    gap: 15px;
  }
  &__img {
    position: relative;
    display: block;
    padding-bottom: 140%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 15px;
  }
  &__icon {
    width: 40px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    img {
      width: 100%;
    }
  }
  &__info {
    background: #fff;
    border-radius: 15px;
    padding: 5px 10px;
    color: #212431;
    font-size: 19px;
    line-height: 130%;
    font-weight: 500;
  }
  &__text {
    font-size: 26px;
    line-height: 130%;
    margin-bottom: 15px;
  }
  &__text2 {
    font-size: 21px;
    line-height: 130%;
    font-weight: 300;
  }
}

@media screen and (max-width: 1200px) {
  .info {
    &__desc {
      width: 300px;
    }
    &__images {
      width: calc(100% - 315px);
    }
    &__text {
      font-size: 24px;
    }
    &__text2 {
      font-size: 19px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding-bottom: 40px;
    &__inner {
      padding: 30px;
    }
    &__group {
      max-width: 600px;
      padding: 30px;
    }
    &__title {
      font-size: 36px;
    }
    &__subtitle {
      font-size: 24px;
      margin-bottom: 30px;
    }
    &__list {
      margin-bottom: 30px;
      li {
        font-size: 16px;
      }
    }
    &__btn {
      height: 42px;
      border-radius: 12px;
      padding: 2px 24px;
      .btn-text {
        font-size: 16px;
      }
    }
  }
  .info {
    padding: 40px 0;
    &__inner {
      flex-direction: column;
    }
    &__desc {
      width: 100%;
    }
    &__images {
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding-bottom: 30px;
    &__inner {
      padding: 25px 15px;
    }
    &__group {
      padding: 20px;
    }
    &__title {
      font-size: 28px;
    }
    &__subtitle {
      font-size: 18px;
      margin-bottom: 20px;
    }
    &__list {
      margin-bottom: 20px;
      li {
        font-size: 14px;
      }
    }
    &__btn {
      height: 38px;
      gap: 5px;
      padding: 2px 24px;
      .btn-text {
        font-size: 14px;
      }
    }
  }
  .info {
    padding: 30px 0;
    &__inner {
      gap: 10px;
      background: #212431;
      border-radius: 20px;
      padding: 25px 15px;
    }
    &__desc {
      background: transparent;
      padding: 0;
    }
    &__images {
      grid-template-columns: repeat(2, calc(50% - 10px));
      gap: 10px;
    }
    &__img:first-child {
      grid-column: 1/3;
    }
    &__icon {
      width: 30px;
    }
    &__info {
      font-size: 14px;
    }
    &__text {
      font-size: 18px;
    }
    &__text2 {
      font-size: 14px;
    }
  }
}