.btn-black__border {
  position: relative;
  background: transparent;
  border: 1px solid #212431;
  border-radius: 15px;
  color: #212431;
  overflow: hidden;
  &:hover {
    box-shadow: 0px 10px 40px -2px rgba(0, 11, 48, 0.3) !important;
  }
  .btn-arrow {
    width: 24px;
    display: flex;
    align-items: center;
    img {
      width: 100%;
    }
  }
  .btn-text {
    line-height: 120%;
  }
  .animation {
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(-30%);
    width: 100%;
    height: 100%;
    animation-name: flash;
    animation-duration: 2.3s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    pointer-events: none;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 12%;
      background: linear-gradient(90deg, rgba(255, 255, 255, .1), rgba(255, 255, 255, .4));
      width: 45px;
      height: 100%;
      transform: skewX(-45deg);
    }
  }
}
.btn-black__green {
  position: relative;
  background: #00aa00;
  border-radius: 15px;
  box-shadow: 0px 0px 5px 5px rgba(33, 36, 49, 0.1);
  color: #fff;
  overflow: hidden;
  .btn-arrow {
    width: 24px;
    display: flex;
    align-items: center;
    img {
      width: 100%;
    }
  }
  .btn-text {
    line-height: 120%;
  }
  .animation {
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(-30%);
    width: 100%;
    height: 100%;
    animation-name: flash;
    animation-duration: 2.3s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    pointer-events: none;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 12%;
      background: linear-gradient(90deg, rgba(255, 255, 255, .1), rgba(255, 255, 255, .4));
      width: 45px;
      height: 100%;
      transform: skewX(-45deg);
    }
  }
}
@keyframes flash {
  0% {
    transform: translateX(-30%);
  }
  30% {
    transform: translateX(105%);
  }
  100% {
    transform: translateX(105%);
  }
}

@media screen and (max-width: 576px) {
  .btn-black__border,
  .btn-black__green {
    border-radius: 10px;
    .btn-arrow {
      width: 20px;
    }
  }
}
